/* ===================
        VARIABLES
====================*/
:root {
  --pink: #D890A8;
  --orange: #F5CDA7;
  --green: #C9CBA3;
  --font: 'Open Sans', helvetica;
  --accent-font: 'Playfair Display';
  --second-accent-font: 'Oswald';
}
/* ===================
        GENERAL
====================*/
body {
  margin: 0;
  padding: 0;
  background: whitesmoke;
  font-family: var(--font);
}
b, strong {
  color: var(--pink);
  font-family: var(--second-accent-font);
  letter-spacing: 1px;
}
/* ===================
        HEADER
====================*/
header img {
  width: 100%;
}
nav {
  margin: 0.7em;
  display: flex;
  justify-content: space-between;
}
nav a {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30%;
  height: 2.5em;
  border-radius: 10px;
  background: var(--orange);
  box-shadow: 1px 1px #ddd;
  color: white;
  font-family: var(--second-accent-font);
  font-weight: 700;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 0.9em;
  letter-spacing: 1px;
  transition: 0.3s ease-in-out;
}
nav a:hover {
  background: var(--pink);
  cursor: pointer;
}
@media (min-width: 800px) {
  header {
    padding: 50px;
    padding-bottom: 0;
    text-align: center;
  }
  header img {
    border-radius: 10px;
  }
  nav {
    margin: 0.7em 0;
  }
}
@media (min-width: 1100px) {
  #root {
    width: 80%;
    background: white;
    margin: 0 auto;
  }
}
/* ===================
        ABOUT
====================*/
.about {
  margin: 0em 2em;
}
.about p, .items-container p {
  font-size: 0.85em;
  color: #444;
}
.about h1 {
  font-family: var(--accent-font);
  font-weight: 900;
  font-size: 1.7em;
  text-transform: lowercase;
  color: #555;
  border-bottom: 3px solid var(--orange);
}
.proof-thumbnails {
  display: grid;
  grid-template-columns: repeat(auto-fit, 100px);
  grid-gap: 1em;
  justify-content: space-between;
}
.proof-thumbnails img {
  align-self: flex-start;
  height: 100px;
  width: 100px;
  object-fit: cover;
  display: block;
  padding: 0.2em;
  background: var(--pink);
  border-radius: 10px;
  transition: 0.3s ease-in-out;
}
.proof-thumbnails img:hover {
  background: var(--orange);
  box-shadow: 2px 2px #eee;
  cursor: pointer;
}
.proofshot-note {
  font-size: 0.7em;
  color: #777;
  margin: 1em 0;
}
.proofshot-modal-container {
  background-color: rgba(255, 255, 255, 0.8);
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  overflow: auto;
  position: fixed;
}
.proofshot-modal {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.proofshot-modal img {
  width: 80%;
  padding: 0.5em;
  background: white;
  border: 2px solid var(--orange);
  border-radius: 10px;
  box-shadow: 1px 1px 1px #ddd;
}
.close-modal {
  position: fixed;
  top: 2em;
  right: 2em;
  height: 30px;
  width: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-family: var(--second-accent-font);
  font-size: 0.8em;
  background: var(--pink);
  border-radius: 50%;
  transition: 0.1s ease-in-out;
}
.close-modal:hover {
  background: var(--orange);
  cursor: pointer;
}
@media (min-width: 500px) {
  .proofshot-modal img {
    width: 75%;
  }
}
@media (min-width: 625px) {
  .proofshot-modal img {
    width: 60%;
  }
}
@media (min-width: 700px) {
  .proofshot-modal img {
    width: 55%;
  }
}
@media (min-width: 800px) {
  .about, .items-container` {
    width: 90%;
    margin: 0 auto;
    padding-bottom: 5em;
  }
  .proofshot-modal img {
    width: 50%;
  }
}
@media (min-width: 950px) {
  .proofshot-modal img {
    width: 40%;
  }
}
@media (min-width: 1200px) {
  .proofshot-modal img {
    width: 35%;
  }
}
/* ===================
        ITEMS
====================*/
.items-container {
  margin: 0em 0.7em;
  padding-bottom: 20px;
}
.items-nav-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.items-nav-bar input[type="text"] {
  padding: 0.6em;
  border: 1px solid #eee;
  border-radius: 10px;
  width: 200px;
}
.items-nav-bar input[type="text"]:focus {
  outline: 0;
  border: 1px solid #ccc;
}
.items-nav-bar i {
  color: var(--pink);
  display: block;
  margin: 0px 0.2em;
  transition: 0.1s ease-in-out;
}
.items-nav-bar i:hover {
  color: #444;
  cursor: pointer;
}
.item-grid {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 1em;
}
.small-item-grid {
  justify-content: space-between;
}
.item {
  width: 90%;
  border-radius: 10px;
  box-shadow: 2px 2px 1px #e9e9e9;
  background: white;
  margin-bottom: 2em;
}
.small-item {
  width: 48%;
}
.item img {
  width: 100%;
  border-radius: 10px 10px 0 0;
}
.quick-info {
  display: flex;
  justify-content: space-between;
  margin: 0.2em 1em;
}
.quick-info h1 {
  margin: 0;
  font-family: var(--accent-font);
  font-weight: 900;
  font-size: 1.5em;
  color: #444;
}
.quick-info span {
  background: var(--pink);
  border-radius: 10px;
  font-size: 0.8em;
  font-weight: 700;
  padding: 0.4em;
  color: whitesmoke;
  display: flex;
  justify-content: center;
  align-items: center;
}
.sale-status {
  margin: 0.5em 1em;
  padding-bottom: 1em;
  border-bottom: 1px solid #eee;
}
.sale-status span {
  background: var(--green);
  border-radius: 10px;
  font-size: 0.6em;
  font-weight: 300;
  text-transform: uppercase;
  padding: 0.5em;
  color: whitesmoke;
}
.etc-info {
  margin: 0.5em 1em;
  font-size: 0.8em;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.etc-info-single {
  border-radius: 11px;
  border: 1px solid #eee;
  width: 47%;
  margin: 0.6em 0;
}
.etc-info-single h2 {
  margin: 0;
  padding: 0.2em;
  background: #888;
  border-radius: 10px 10px 0 0;
  color: white;
  font-family: var(--second-accent-font);
  font-size: 1.2em;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 1px;
}
.etc-info-single p {
  font-size: 0.9em;
  margin: 0;
  padding: 0.5em;
  text-align: center;
}
.etc-info-single a {
  text-decoration: none;
  font-weight: 700;
  color: var(--pink);
}
.item-note {
  margin: 0.5em 1em;
  font-size: 0.7em;
  color: #888;
}
.item-note h3 {
  margin: 0;
  font-size: 1.3em;
  font-family: var(--second-accent-font);
  text-transform: uppercase;
  color: var(--orange);
}
.item-note ul {
  list-style-type: none;
  margin: 0;
  padding: 0.3em;
}
.item-note li:before {
  content: '* '
}
